.page-header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem;
  z-index: 20;
  // overflow: hidden;
}

.page-titel {
  position: relative;
  @include hideText();
  display: block;
  width: 85px;
  height: 40px;
  background-image: url(../media/nerddisco-logo.svg);
  background-size: auto 100%;
  background-position: 0 50%;
  background-repeat: no-repeat;
  z-index: 3;
}

.page-nav {
  position: relative;
  width: calc( 100% - 90px );
  z-index: 3;
}

.page-nav ul { text-align: right; }

.page-nav ul li {
  display: inline-block;
  padding-right: 0.2rem;

  @include bpMedium {
    padding-right: 1rem;
  }
}

.page-nav ul li a {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    right: 0;
    bottom: -0.4rem;
    left: 0;
    height: 1px;
    background-color: $dark-muted;
    opacity: 0;
    transform: translateY(0.5rem);
    transition: transform 0.25s, opacity 0.25s;
  }

  &:hover:after,
  &:focus:after,
  &:active:after {
    opacity: 1;
    transform: translateY(0);
  }

  &[href^="http"] {
    color: $dark-muted;
  }
}



.bgslide {
  position: absolute;
  right: 0;
  left: 0;
  background-color: $dark;
  // box-shadow: inset 0 -1px 0 $dark-muted;
  background-image: linear-gradient(90deg, $dark, $dark-muted, $dark);
  background-position: 50% 100%;
  background-repeat: no-repeat;
  background-size: 100% 1px;
  opacity: 1;
  z-index: 1;
}
